/*
 * BCGSC - IPR-Client User API
 *
 * This API factory implements the IPR-API. Calls to and from the API are
 * managed through this construct.
 *
 */
app.factory('api.summary.genomicEventsTherapeutic', ['_', '$http', '$q', (_, $http, $q) => {
  
  const api = CONFIG.ENDPOINTS.API + '/POG';
  
  let $get = {};
  
  
  /*
   * Get Genomic Events with Therapeutic Association
   *
   * Retrieve all POGs from API that user can access
   *
   */
  $get.all = (POGID, report) => {
    return $q((resolve, reject) => {
      
      // Retrieve from API
      $http.get(api + '/' + POGID + '/report/' + report + '/genomic/summary/genomicEventsTherapeutic').then(
        (result) => {
          // Load into cache
          
          resolve(result.data);
        },
        (error) => {
          // TODO: Better error handling
          reject(error);
        }
      );
      
    });
    
  }
  
  /*
   * Get an Genomic Event with Therapeutic Association
   *
   * @param string POGID - POGID, eg POG129
   * @param string ident - UUID4 identity string for entry
   *
   */
  $get.id = (POGID, report, ident) => {
    
    return $q((resolve, reject) => {
            
      // Get result from API
      $http.get(api + '/' + POGID + '/report/' + report + '/genomic/summary/genomicEventsTherapeutic/' + ident).then(
        (result) => {
          resolve(result.data);
        },
        (error) => {
          // TODO: Better error handling
          reject();
        }
      );
    });
  }
  
  /*
   * Update an Genomic Event with Therapeutic Association
   *
   * @param string POGID - POGID, eg POG129
   * @param string ident - UUID4 identity string for entry
   *
   */
  $get.update = (POGID, report, ident, get) => {
    
    return $q((resolve, reject) => {
      
      // Get result from API
      $http.put(api + '/' + POGID + '/report/' + report + '/genomic/summary/genomicEventsTherapeutic/' + ident, get).then(
        (result) => {

          resolve(result.data);
        },
        (error) => {
          // TODO: Better error handling
          reject();
        }
      );
    });
  }
  
  /*
   * Remove an Genomic Event with Therapeutic Association
   *
   * @param string POGID - POGID, eg POG129
   * @param string ident - UUID4 identity string for entry
   *
   */
  $get.remove = (POGID, report, ident) => {
    
    return $q((resolve, reject) => {
      
      // Get result from API
      $http.delete(api + '/' + POGID + '/report/' + report + '/genomic/summary/genomicEventsTherapeutic/' + ident).then(
        (result) => {
          resolve(true);
        },
        (error) => {
          // TODO: Better error handling
          reject();
        }
      );
    });
  }
  
  return $get;
  
}]);
